(function(){
    'use strict';
    require('angular-ui-router');
    require('services/api/api.services');
    var auditsDirective = require('directives/audits/audits.directive');
    require('app.scss');

    var MODULE_NAME = 'app.views.user-scac-details';

    angular.module(MODULE_NAME,[
        'ui.router',
        'app.services',
        auditsDirective
    ]).config(Config).controller('UserScacDetailsCtrl',Controller);

    /* @ngInject */
    function Controller($rootScope,$scope,$log,user_scac_data,AppServices) {

        $scope.user_scac = user_scac_data;
        $scope.audits = [];
        $scope.default_config = AppServices.api.shortfuse_configs.default_config;

        function Init() {

            //$log.debug('user_scac_data: ' + JSON.stringify(user_scac_data,null,2));

            //$log.debug('AppServices.api.shortfuse_configs.default_config: ' + JSON.stringify(AppServices.api.shortfuse_configs.default_config,null,2));

            $rootScope.showActivity = true;

            AppServices.api.user_scacs.get_audits({id:$scope.user_scac.id},function(result){

                $scope.audits = result;
                $rootScope.showActivity = false;
            })

        };

        $scope.isDisabled = function (user_scac) {

            return AppServices.api.user_scacs.isDisabled(user_scac);
        };

        $scope.startQuery = function () {
            $rootScope.showActivity = true;
            return AppServices.api.user_scacs.start_query({id: $scope.user_scac.id}).$promise.finally(function () {
                $rootScope.showActivity = false;
                $scope.refreshUserScac();
            });

        };

        $scope.stopQuery = function () {
            $rootScope.showActivity = true;
            return AppServices.api.user_scacs.stop_query({id: $scope.user_scac.id}).$promise.finally(function () {
                $rootScope.showActivity = false;
                $scope.refreshUserScac();
            });

        };

        $scope.refreshUserScac = function () {
            $rootScope.showActivity = true;
            AppServices.api.user_scacs.get({id:$scope.user_scac.id},function(result){
                $scope.user_scac = result;
                $rootScope.showActivity = false;
            });
        };

        Init();

    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.user-scac-details', {
                url: '/user-scacs/:id',
                views: {
                    'container@': {
                        template: require('./user-scac-details.html'),
                        controller: 'UserScacDetailsCtrl',
                        controllerAs: 'vm'
                    }
                },
                resolve:{
                    user_scac_data:function($log,$stateParams,AppServices){

                        //$log.debug('$stateParams: ' + JSON.stringify($stateParams,null,2));
                        return AppServices.api.user_scacs.get({id:$stateParams.id}).$promise;
                    }
                }
            });
    };

    module.exports = MODULE_NAME;

})();


